.game{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

.grid{
    margin: 15px auto;
    background-color: black;
}

.cell{
    margin: 0;
    padding: 0;
    border: none;
}

.row-grid{
  height: 2.5rem;
}

img, .cell {
  width: 2.5rem;
  height: 2.5rem;
}

.cell-current{
  background-color: lightgreen;
}


.cell-wall{
  background-color: rgb(90, 90, 90);
}

.controls{
  position: fixed;
  bottom: 5px;
  left: 45%;
}

.button{
  width: 4rem;
  height: 4rem;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid rgb(70, 70, 70);
  background-color: rgb(37, 37, 37);
  color: darkgoldenrod;
  margin: 1px;
  padding: 0;
  opacity: 0.65;
}

.button:hover{
  border-color: rgb(131, 131, 131);
}


@media only screen and (max-width: 800px) {
  .grid{
    margin-top: 0px;
  }
  .cell, img{
    
    width: 2rem;
    height: 2rem;
  }
  .row-grid{
    height: 2rem;
  }
  .controls{
    left: 40%;
  }
}
